@media print {
    body {
        -webkit-print-color-adjust: exact;
        /*chrome*/
        zoom: .8;
        border-top: none;
    }
    .bgImg {
        display: list-item;/* list-item */
        list-style-image: url(print_bg.png);/*  */
        list-style-position: inside;/*  */
    }
    header{
        display: none;
    }
    footer{
        display: none;
    }
    .footerTelSp{
        display: none;
    }
    .menu-btn{
        display: none;
    }
    .fixBar{
        display: none !important;
    }
    .parallax-mirror {
        display: none;
        height: 0 !important;
    }
    .rellax{
        transform: none !important;
    }
    // 
    .PrintHidden {
        display: none;
    }
    // 
    .printArea{
        //top:0 !important;
        //left:0 !important;
        //width:172mm !important;
        //height:251mm !important;
        margin: 0;
        padding: 0;
    }    
}
